<template>
  <div class="login">
      <mt-cell title="登录"></mt-cell>
      <div class="loginBody">
        <a class="field" style="margin-top:10%;">
        <div class="img"><img :src="phoneSrc" alt=""></div>
        <div class="wraper">
          <div class="value">
            <input v-model="username" @focus="phoneFocus" @blur="phoneBlur" placeholder="输入注册手机号" type="text" class="core">
          </div>
        </div>
        </a>
        <a class="field">
          <div class="img"><img :src="passwdSrc" alt=""></div>
          <div class="wraper">
            <div class="value">
              <input v-model="password" v-if="show" @focus="passwdFocus" @blur="passwdBlur" placeholder="输入密码" type="text" class="core">
              <input v-model="password" v-else @focus="passwdFocus" @blur="passwdBlur" placeholder="输入密码" type="password" class="core">
            </div>
          </div>
          <div class="img" @click="showPasswd" style="margin-right:5%;"><img :src="passwdEyeSrc" alt=""></div>
        </a>
        <mt-button @click.native="submitLogin" size="large">登录</mt-button>
        <div class="btns">
          <mt-button @click.native="forgetPasswd">忘记密码</mt-button>
          <mt-button @click.native="register">立即注册</mt-button>
        </div> 
      </div> 
      <div class="footer">
        <img src="../assets/bg.png" alt="">
      </div>  
  </div>
</template>

    <script>
    import { mapGetters, mapActions} from 'vuex'
    import { Toast } from 'mint-ui';
    export default {
    name: 'login',
    data () {
      return {
        username: '',
        password: '',
        app: 'client',
        phoneSrc: '/static/userDefault.png',
        passwdSrc: '/static/passwd.png',
        passwdEyeSrc: '/static/passwdHide.png',
        show: false
      }
    },
    computed: {
      ...mapGetters({
        glodalVal: 'getGlodalVal'
      })
    },
    methods: {
      phoneFocus: function() {
        this.phoneSrc = '/static/uesrNow.png';
      },
      phoneBlur: function() {
        this.phoneSrc = '/static/userDefault.png';
      },
      passwdFocus: function() {
        this.passwdSrc = '/static/passwdNow.png';
      },
      passwdBlur: function() {
        this.passwdSrc = '/static/passwd.png';
      },
      showPasswd: function() {
        this.passwdSrc = '/static/passwdNow.png';
        if(this.passwdEyeSrc == '/static/passwdHide.png') {
          this.passwdEyeSrc ='/static/passwdShow.png';
          this.show = true;
        } else {
          this.passwdEyeSrc ='/static/passwdHide.png';
          this.show = false;
        }
      },
      ...mapActions({
        login: 'login',
        changeToken: 'changeToken',
        changeAvatar: 'changeAvatar',
        changePhone: 'changePhone'
      }),
     submitLogin: function() {
      let obj = {};
      obj.username = this.username;
      obj.password = this.password;
      obj.app = this.app;
      this.login(obj).then(response => {
        sessionStorage.setItem('token', response.data.token);
        sessionStorage.setItem('avatar', response.data.avatar);
        sessionStorage.setItem('phone', response.data.phone);
        // this.changeToken(response.data.token);
        // this.changeAvatar(response.data.avatar);
        this.changePhone(response.data.phone);
        this.$router.push({path: '/index'});
      },error => {
        if(error.body.code == 1001) {
          Toast({
            message: '用户名或密码输入不合法',
            position: 'bottom',
            duration: 2000
          });
        }
        if(error.body.code == 2002) {
          Toast({
            message: '用户名或密码错误',
            position: 'bottom',
            duration: 2000
          });
        }
      })
     },
     forgetPasswd: function() {
      this.$router.push({path: '/forgetPasswd'})
     },
     register: function() {
      this.$router.push({path: '/register'})
     }
    }
    }
    </script>
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style>
* {
  margin: 0px;
  padding: 0px;
}
.login .mint-cell {
  padding: 3% 0;
  padding-left: 2%;
}
.btns {
  display: flex;
  justify-content: space-between;
}
.login .mint-button{
  margin-top: 6%;
  background-color: #fff; 
  border-radius: 8px;
  border: 1px solid #dad2da;
  color: #d06091;
}
.login .mint-button::after {
  background-color: #f1cfde;
}
.loginBody {
  padding: 2%;
}
.login .btns .mint-button {
  border: none;
  outline: none;
  box-shadow: none;
}
.login .btns .mint-button::after {
  background-color: #fff;
}
.footer {
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  z-index: 1;
  width: 100%;
}
.footer img {
  width: 100%;
}
/**/
.login .mint-cell-text {
  font-size: 0.4rem;
}
.login .mint-button-text {
  font-size: 0.4rem;
}
.login .mint-button {
  height: 1rem;
}
</style>



